@import url();

@font-face{
  font-family: 'Audimat';
  src: url(/static/media/Audimat-Mono.ttf.1686c2dd.woff) format("woff")
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

@media only screen and (max-width: 600px) {
  .title-padding{
    display: none
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#root{
  height: 100%;
}

html, body {
  margin: 0;
  height: 100%;
  padding: 1em;
}

.ReactGridGallery_tile-viewport:hover{
  outline: 1px solid #739eae
}

.ReactGridGallery{
    margin: auto;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


.slick-container {
    height: 60%;
  }
  .slick-slider, .slick-list, .slick-track {
    height: 100%;
  }


.toolbar_Container{
    height: 100%;
}
